import styled from "styled-components";

import { Box } from "../FlexBox";
import { text_12px_Medium } from "../Text";

export const Button = styled(Box).attrs({
  as: "button",
})`
  position: relative;
  overflow: hidden;
  z-index: 1;

  flex-direction: column;
  padding: 0.3rem 0.6rem;

  border-radius: 4px;
  background-color: rgb(${(props) => props.theme.color.ultramarineBlue});

  transition: background-color 150ms ease-in;
  cursor: pointer;

  &:hover {
    background-color: rgb(${(props) => props.theme.color.blueberry});
  }

  &::after {
    position: absolute;
    z-index: -1;
    top: 0;

    width: 100%;
    height: 100%;
    background-color: rgba(${(props) => props.theme.color.cornflowerBlue}, 0);

    content: "";
  }

  &:active::after {
    background-color: rgba(${(props) => props.theme.color.cornflowerBlue}, 1);
  }
`;

export const ButtonName = styled.span`
  ${text_12px_Medium};
  color: rgb(${({ theme }) => theme.color.cultured});
`;
